
const state = {
    componentMenu: [],
    withRates: [],
    classificator: [],
};

// getters
const getters = {
    componentMenu: state => state.componentMenu
};

// actions
const actions = {

};

// mutations
const mutations = {
    setupToolbarMenu(state, actions) {
        state.componentMenu = actions;
    }
};

export default {
    state,
    getters,
    actions,
    mutations,
};



